const radioBtn = Array.from(document.querySelectorAll(".percent"))
const numPeople = document.querySelector(".num__people")
const bill = document.querySelector(".bill")
const custom = document.querySelector(".custom")
const resetBtn = document.querySelector(".reset")
const numberRegex = /^\s*[+-]?(\d+|\.\d+|\d+\.\d+|\d+\.)(e[+-]?\d+)?\s*$/
let validate = false

//console.log(bill)
let totalAmount = 0
let totalTipAmount = 0
let totalAmountWithTip = 0
let tipPerPerson = 0
let totalPerPerson = 0

console.log(validate)
if (validate) {
    resetBtn.disabled = false
} else {
    resetBtn.disabled = true
}

radioBtn.forEach(item => {
    item.addEventListener("change", (evt) => {
        calculateBill(evt.target)
    })
})

bill.addEventListener("input", (evt) => {
    const validNum = numberRegex.test(bill.value)
    const billErr = document.querySelector(".bill__err")
    console.log(validNum)
    if (validNum) {
        if (Number(bill.value) <= 0) {
            console.log("not a number")
            billErr.classList.remove("hide__err")
            bill.classList.remove("correct__format")
            bill.classList.add("wrong__format")
            billErr.innerHTML = "Cannot be zero or less"
        } else {
            billErr.classList.add("hide__err")
            bill.classList.add("correct__format")
            bill.classList.remove("wrong__format")
            calculateBill(bill)
        }
    }
    else {
        // console.log("not a number")
        billErr.classList.remove("hide__err")
        bill.classList.remove("correct__format")
        bill.classList.add("wrong__format")
        billErr.innerHTML = "Not a number"
    }

})

numPeople.addEventListener("input", (evt) => {
    const validNum = numberRegex.test(numPeople.value)
    const peopleErr = document.querySelector(".people__err")
    if (validNum) {
        if (Number(numPeople.value) <= 0) {
            console.log("not a number")
            peopleErr.classList.remove("hide__err")
            numPeople.classList.remove("correct__format")
            numPeople.classList.add("wrong__format")
            numPeople.innerHTML = "Number can not be zero or less"
        } else {
            peopleErr.classList.add("hide__err")
            numPeople.classList.add("correct__format")
            numPeople.classList.remove("wrong__format")
            calculateBill(bill)
        }
    }
    else {
        console.log("not a number")
        peopleErr.classList.remove("hide__err")
        numPeople.classList.remove("correct__format")
        numPeople.classList.add("wrong__format")
        peopleErr.innerHTML = "Not a number"
    }
})

custom.addEventListener("focus", (evt) => {
    // console.log(evt.target)
    radioBtn.forEach(item => {
        item.checked = false
    })

})
custom.addEventListener("input", (evt) => {
    // console.log(custom.value)
    calculateBill(custom)
})
function calculateBill(billedAmount) {
    validate = false
    //  console.log(billedAmount) //evt.target
    //   console.log(bill.value, typeof bill.value)
    //    console.log(billedAmount.value, typeof billedAmount.value) //console.log(evt.target.value, typeof evt.target.value) 
    //  console.log(numPeople.value, typeof numPeople.value)

    if (bill.value === "" || Number(bill.value) <= 0) {
        //if the bill is an empty string (no data has been entered) or
        // if the bill entered is less that or equal to 0 , then return without doing anything
        // console.log("escape")
        return false
    }
    else {
        totalAmount = Number(bill.value)
        //  console.log(bill.value !== "" || Number(bill.value) >= 0)
        //   console.log(`total amount is: ${totalAmount}`)
        if (numPeople.value === "" || Number(numPeople.value) <= 0) {
            // console.log("do nothing")
            // validate = false
            return false
        } else {
            totalTipAmount = totalAmount * Number(billedAmount.value) / 100 //evt.target
            totalAmountWithTip = totalAmount + totalTipAmount
            tipPerPerson = totalTipAmount / Number(numPeople.value)
            totalPerPerson = totalAmountWithTip / Number(numPeople.value)
            //  console.log(numPeople.value !== "" && Number(numPeople.value) >= 0)
            // console.log(`Total tip is : ${totalTipAmount}`)
            // console.log(`Total amount with tip is : ${totalAmountWithTip}`)
            // console.log(`Tip amount per person : ${tipPerPerson}`)
            //   console.log(`Total amount per person : ${totalPerPerson}`)
            validate = true

            if (validate) {
                resetBtn.disabled = false
            } else {
                resetBtn.disabled = true
            }

            display()
            return true
        }
    }

}

function display() {
    const dollarsUS = Intl.NumberFormat("en-US", {
        style: "currency",
        currency: "USD",
    })
    const displayTip = document.querySelector(".tip__value")
    const displayTotalAmount = document.querySelector(".total__value")
    displayTip.innerHTML = dollarsUS.format(tipPerPerson)
    displayTotalAmount.innerHTML = dollarsUS.format(totalPerPerson)
}